

import { useState } from "react";
import About from "./components/Main/about";
import Contact from "./components/Main/contact";
import Navbar from "./components/Main/navbar";
import Projects from "./components/Main/projects";
import Resume from "./components/Main/resume";
import Sidebar from "./components/Sidebar/sidebar";

export default function Home() {
  const [activeComponent, setActiveComponent] = useState("About"); // Default to "About"

  return (
    <div className="min-h-screen bg-gradient-to-r from-green-400 to-blue-400 p-10 px-20">
      <div className="grid grid-cols-12 gap-6">
        {/* Sidebar */}
        <div className="lg:col-span-3 col-span-12 bg-white rounded-2xl shadow-xl p-4">
          <Sidebar />
        </div>

        {/* Main Content */}
        <div className="lg:col-span-9 col-span-12 bg-white rounded-2xl shadow-xl p-4">
          <Navbar setActiveComponent={setActiveComponent} />

          {/* Conditionally Render Components */}
          {activeComponent === "About" && <About />}
          {activeComponent === "Projects" && <Projects />}
          {activeComponent === "Resume" && <Resume />}
          {activeComponent === "Contact" && <Contact />}
        </div>
      </div>
    </div>
  );
}
